*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,body{
 background-color: rgba(0,0,0,255);
}

.App{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  font-family: Lato,sans-serif;
}

.logo-contenedor{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
}

.witcher-logo{
  height: auto;
}

.contenedor-contador{
  height: 550px;
  min-width: 600px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}